@import url(https://fonts.googleapis.com/css?family=Muli:400,700);
.week-number {
  position: absolute;
  top: 0;
  left: 0;
}

.week-number > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.week-number--current {
  width: 3rem;
  height: 3rem;
  background-color: yellow;
}

.week-number--navigation {
  background-color: #191414;
  height: 1.5rem;
  color: white;
  text-decoration: none;
}

.week-number--navigation:hover {
  background-color: white;
  color: #191414;
}

.backdrop {
  background-size: 150%;
  background-position: 50%;
  /* Add the blur effect */
  filter: blur(0.75);
  -webkit-filter: blur(0.75rem);
  position: fixed;
  top: -2rem;
  left: -2rem;
  bottom: -2rem;
  right: -2rem;
  z-index: -1;
}

.album-info {
  margin: auto auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 800px;
}

.album-info--cover img {
  max-width: 19rem;
  width: 100%;
}

.album-info--meta {
  padding-left: 2rem;
  color: #fff;
}

.album-info--meta .heading {
  font-size: 3rem;
  margin: 0 0 1rem 0;
}

.album-info--meta .subheading {
  font-size: 1.5rem;
  margin: 1rem 0 2rem 0;
}

.album-info--meta .subheading .year {
  font-style: italic;
  opacity: 0.5;
}

.external-action {
  display: inline-block;
  padding: 1rem;
  background: #191414;
  border-radius: 0.1875rem;
  color: white;
  font-size: 1.125rem;
  text-decoration: none;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
  margin-right: 1rem;
}

.external-action:hover {
  color: #191414;
  background: white;
}

.external-action svg {
  margin-right: 1rem;
  display: inline-block;
  vertical-align: middle;
}

@media (max-width: 768px) {
  .album-info {
    margin-top: 10rem;
    flex-direction: column;
    padding: 2rem;
  }

  .album-info--meta {
    margin-top: 1rem;
    padding: 0;
  }

  .album-info--meta .heading {
    font-size: 2rem;
  }

  .external-action {
    font-size: 1rem;
    margin-bottom: 1rem;
    padding: 0.75rem;
  }
}

.loading-cover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #191414;
}

.loading-anim-leave {
  opacity: 1;
}

.loading-anim-leave.loading-anim-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.loading-message {
  color: #fff;
  font-size: 1.5rem;
  font-style: italic;
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.loading-anim-leave.loading-anim-leave-active .loading-message {
  -webkit-transform: translate3d(0, 1rem, 0);
          transform: translate3d(0, 1rem, 0);
  opacity: 0.01;
  -webkit-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
}

.progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0.5rem;
}

.progress--bar {
  height: 0.5rem;
  background: yellow;
  -webkit-transition: width 0.1s ease;
  transition: width 0.1s ease;
}

.loading-anim-leave.loading-anim-leave-active .progress--bar {
  height: 1px;
  -webkit-transition: height 100ms ease-in;
  transition: height 100ms ease-in;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Muli", sans-serif;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
}

.byline {
  opacity: 0.75;
  align-self: end;
  justify-self: flex-end;
  margin-top: auto;
  color: white;
  font-size: 1rem;
  padding: 0.5rem;
}

.byline a {
  color: yellow;
  text-decoration: none;
}

.byline a:hover {
  color: white;
}

@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  .wrapper {
    overflow: scroll;
  }
}

@media (max-height: 500px) {
  .wrapper {
    position: static;
  }
}

